<template>
  <div class="indatail" v-show="isShowHtml">
    <div>
      <van-nav-bar :title="cartoonItemData.title" left-arrow @click-left="onClickLeft" fixed />
    </div>
    <div class="auto-img">
      <img v-lazy="cartoonItemData.horizontal_cover" />
      <!-- <img :src="cartoonItemData.horizontal_cover" alt /> -->
      <div class="title-box">
        <div class="title">{{cartoonItemData.title}}</div>
        <div class="text-box">
          <span
            class="auter"
            v-for="(auterItem,auterIndex) in cartoonItemData.author_name"
            :key="'auter' + auterIndex"
          >{{auterItem}}·</span>
          <span v-for="(typeItem,typeIndex) in cartoonItemData.styles" :key="typeIndex">{{typeItem}}</span>
        </div>
      </div>
    </div>

    <!-- 收藏与观看历史章节 -->
    <div>
      <van-goods-action>
        <van-goods-action-icon
          v-if="isCollect"
          color="#ffe697"
          icon="star-o"
          text="追漫"
          @click="addCartoon"
        />
        <van-goods-action-icon v-else color="#ffe697" icon="star" text="已追" @click="removeCartoon" />

        <van-goods-action-button
          v-if="this.lookIndex === null"
          color="#fb7299"
          type="danger"
          @click="onLook"
          text="开始阅读'"
        />
        <van-goods-action-button
          v-else
          color="#fb7299"
          type="danger"
          @click="onLook"
          v-text="'续看' + LookHistoryData.short_title + '话'"
        />
      </van-goods-action>
    </div>

    <!-- 是否完结 -->
    <div class="isFinish-box">
      <div class="isFinish" v-if="cartoonItemData.is_finish === 1">已完结</div>
      <div class="isFinish" v-else>连载中</div>
    </div>
    <!-- 漫画介绍 -->
    <div class="introduce-box">
      <div class="introduce">{{cartoonItemData.classic_lines}}</div>
    </div>

    <!-- 章节显示 -->
    <div class="chapter-box">
      <van-collapse v-model="activeName" accordion>
        <van-collapse-item :title="'全部章节(' + (this.cartoonItemData.last_ord) + ')'" name="1">
          <!-- 列表排序 -->
          <div class="isSort">
            <span v-if="isSort" @click="listSort">
              <van-icon name="ascending" />升序
            </span>
            <span v-else @click="listSort">
              <van-icon name="descending" />降序
            </span>
          </div>
          <!-- 章节列表 -->
          <div class="epList-box">
            <div
              class="list-item"
              v-for="(epItem,epIndex) in cartoonItemData.ep_list"
              :key="epIndex"
              :name="epItem.id"
              @click="goLookCartoon(epItem.id,epItem.title)"
            >
              {{epItem.short_title}}
              <div class="icon-box">
                <van-icon color="#f9a018" :name="epItem.is_locked === true ? 'lock' : '' " />
              </div>
            </div>
          </div>
        </van-collapse-item>
      </van-collapse>
    </div>

    <!-- 讨论区 -->
    <div class="forum-box">
      <div class="title">
        讨论区
        <span>({{amount}})</span>
      </div>
      <div class="content-box">
        <div class="forum-item" v-for="(forumItem,forumIndex) in forumData" :key="forumIndex">
          <div class="user">
            <img v-lazy="forumItem.member.avatar" />
            <span>{{forumItem.member.uname}}</span>
          </div>
          <div class="text">{{forumItem.content.message}}</div>
        </div>
      </div>
    </div>
    <!-- 猜你喜欢 -->
    <div class="live-box">
      <div class="title">猜你喜欢</div>
      <div class="live-list">
        <div
          class="live-item"
          v-for="(liveItem,liveIndex) in liveData"
          :key="liveIndex"
          @click="goCartoon(liveItem.id)"
        >
          <div class="auto-img">
            <img v-lazy="liveItem.vertical_cover" />
            <!-- <img :src="liveItem.vertical_cover" alt /> -->
          </div>
          <div class="content-text">
            <div class="name">{{liveItem.title}}</div>
            <div class="update">
              <span v-if="liveItem.is_finish === 1">[完结] 共</span>
              <span v-else>更新至</span>
              {{liveItem.last_short_title}}话
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { set } from "vue";
export default {
  name: "Indetail",
  data() {
    return {
      cartoonItemData: {},
      ComicDetail: [],
      activeName: ["1"],
      isCollect: true,
      // 是否显示渲染数据
      isShowHtml: false,

      // 章节列表排序
      isSort: true,
      id: null,
      // 讨论区数据列表
      forumData: [],
      // 讨论区数量
      amount: "",
      // 相关漫画数据
      liveData: [],
      // 当前漫画所有章节数据
      LookHistoryData: [],
      lookIndex: null
    };
  },
  created() {
    let id = this.$route.params.id;
    this.$root.nowIndetailId = this.$route.params.id;
    this.id = Number(id);
    this.getCartoonItem(id);
    this.getForumList(id);
    this.getLiveList(id);

    let arr = JSON.parse(localStorage.getItem("BookrackData"));
    this.$root.bookData = Array.from(new Set(arr));

    let storage = localStorage;
    //  判断漫画是否已被收藏
    if (this.$root.bookData.indexOf(this.id) !== -1) {
      this.switchover();
    }

    //  判断是否保存观看历史
    if (storage.LookHistory === undefined) {
      localStorage.setItem("LookHistory", "[]");
    } else {
      let LookHistoryArr = JSON.parse(localStorage.getItem("LookHistory"));
      //
      LookHistoryArr.forEach(item => {
        if (item.id === id) {
          this.lookIndex = item.index;
        }
      });
      this.$root.LookHistory = Array.from(new Set(LookHistoryArr));
    }
  },
  methods: {
    // 收藏漫画
    addCartoon() {
      this.isCollect = !this.isCollect;
      //
      this.$root.bookData.push(this.id);
      localStorage.setItem("BookrackData", JSON.stringify(this.$root.bookData));
    },
    // 移除收藏漫画
    removeCartoon() {
      this.isCollect = !this.isCollect;
      this.$root.bookData.splice(this.$root.bookData.indexOf(this.id), 1);
      //
      localStorage.setItem("BookrackData", JSON.stringify(this.$root.bookData));
    },
    //  漫画是否已被收藏,反转显示
    switchover() {
      this.isCollect = false;
    },
    goCartoon(id) {
      this.$router.push({
        name: "Indetail",
        params: { id: id }
      });
      this.$router.go(0);
    },
    // 点击查看章节内容
    // 当前漫画数据
    goLookCartoon(id, title) {
      let index = this.$root.ListIdData.findIndex(item => item.id == id);

      let lookArr = { id: this.$root.nowIndetailId, index: index };
      // 获取当前观看数据下标
      let indexId = this.$root.LookHistory.findIndex(
        item => item.id == lookArr.id
      );
      // 更新观看历史
      if (indexId === -1) {
        this.$root.LookHistory.push(lookArr);
        localStorage.setItem(
          "LookHistory",
          JSON.stringify(this.$root.LookHistory)
        );
      } else {
        this.$root.LookHistory.splice(indexId, 1);
        this.$root.LookHistory.push(lookArr);
        localStorage.setItem(
          "LookHistory",
          JSON.stringify(this.$root.LookHistory)
        );
      }

      // 跳转章节漫画
      this.$router.push({
        name: "Watch",
        params: {
          id,
          title,
          // 当前漫画详情的id
          nowIndetailId: this.$root.nowIndetailId
        }
      });
    },
    // 观看历史章节
    onLook() {
      //
      // console.log(this.LookHistoryData);
        this.$router.push({
          name: "Watch",
          params: {
            id: this.LookHistoryData.id,
            title: this.LookHistoryData.title,
            // 当前漫画详情的id
            nowIndetailId: this.$root.nowIndetailId
          }
        });
    },
    //  章节列表排序
    listSort() {
      this.isSort = !this.isSort;
      this.getCartoonItem(this.id);
    },
    // 获取漫画讨论区
    getForumList(id) {
      this.axios({
        method: "get",
        url: "/ReplyMain",
        params: {
          oid: id
        }
      })
        .then(res => {
          this.forumData = res.data.data.replies;
          //
          if (res.data.data.cursor.all_count > 10000) {
            this.amount =
              (res.data.data.cursor.all_count / 10000).toFixed(1) + "W";
          } else if (res.data.data.cursor.all_count > 1000) {
            this.amount =
              (res.data.data.cursor.all_count / 1000).toFixed(1) + "K";
          } else if (res.data.data.cursor.all_count > 0) {
            this.amount = res.data.data.cursor.all_count;
          } else {
            this.amount = "暂无";
          }
        })
        .catch(err => {});
    },

    // 获取漫画详情
    getCartoonItem(id) {
      this.axios({
        method: "get",
        url: "/ComicDetail",
        params: {
          comicId: id
        }
      })
        .then(res => {
          this.$root.ListIdData = [];
          this.LookHistoryData = [];
          this.cartoonItemData = res.data.data;

          this.$root.epListData = res.data.data.ep_list;
          // 获取所有章节
          let ArrayData = [];
          this.cartoonItemData.ep_list.forEach(item => {
            ArrayData.unshift(item);
          });
          if(this.lookIndex === null){
             this.LookHistoryData = ArrayData[0];
          }else{
             this.LookHistoryData = ArrayData[this.lookIndex];
          }

          this.cartoonItemData.ep_list.forEach(item => {
            this.$root.ListIdData.unshift({
              id: item.id,
              title: item.title
            });
          });

          this.isShowHtml = true;
          //  判断章节列表排序
          if (this.isSort) {
            res.data.data.ep_list.reverse();
          } else {
            res.data.data.ep_list;
          }
        })
        .catch(err => {});
    },
    //  相关喜欢漫画数据
    getLiveList(id) {
      this.axios({
        method: "get",
        url: "/MoreRecommend",
        params: {
          comicId: id
        }
      })
        .then(res => {
          this.liveData = res.data.data.recommend_comics.slice(0, 6);
          //
        })
        .catch(err => {});
    },

    // 返回上一级
    onClickLeft() {
      this.$router.push({
        name: "Home"
      });
      // this.$router.back();
    }
  }
};
</script>

<style lang="scss" scoped>
.indatail {
  .auto-img {
    position: relative;
    color: white;
    margin-top: 46px;
    .title-box {
      width: 90%;
      position: absolute;
      left: 10px;
      bottom: 10px;
      overflow: hidden;
      white-space: nowrap;
      text-overflow: ellipsis;
      text-shadow: 0 1px 2px rgba(0, 0, 0, 0.5);
      .title {
        font-size: 20px;
        font-weight: 500;
        margin-bottom: 10px;
      }
      .text-box {
        font-size: 14px;
      }
    }
  }
  .van-goods-action {
    position: static;
    margin-top: 15px;
    ::v-deep .van-goods-action-icon {
      width: 2rem;
      flex-direction: row;
      align-items: center;
      font-size: 16px;
      margin-left: 0.1rem;
    }
    ::v-deep .van-goods-action-icon__icon {
      font-size: 30px;
    }
    ::v-deep .van-goods-action-button--first {
      margin-left: 1rem;
    }
  }

  .isFinish-box {
    font-size: 16px;
    margin: 5px 20px;
  }
  .introduce-box {
    font-size: 10px;
    color: #999;
    margin: 10px 20px;
  }
  .chapter-box {
    .isSort {
      text-align: right;
      padding: 5px 5px;
    }
    .epList-box {
      display: flex;
      flex-wrap: wrap;
      // justify-content: space-between;
      .list-item {
        width: calc(25% - 7px);
        text-align: center;
        box-sizing: border-box;
        padding: 10px;
        font-size: 15px;
        border-radius: 0.25rem;
        border: 1px solid #ccc;
        margin-right: 9px;
        margin-bottom: 5px;
        position: relative;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        cursor: pointer;
        &:nth-child(4n) {
          margin-right: 0;
        }
        .icon-box {
          width: 10%;
          height: 10%;
          position: absolute;
          top: -5%;
          right: 10%;
        }
      }
    }
    ::v-deep .van-cell__title {
      font-size: 15px;
    }
  }
  .forum-box {
    width: 100%;
    height: 150px;
    margin-top: 20px;
    .title {
      font-size: 0.42667rem;
      margin: 0.23333rem 0.43333rem;
    }
    .content-box {
      width: auto;
      white-space: nowrap;
      overflow-x: scroll;
      &::-webkit-scrollbar {
        width: 10px;
        height: 5px;
      }
      &::-webkit-scrollbar-thumb {
        border-radius: 5px;
        background: #cecece;
      }
      &::-webkit-scrollbar-track {
        background: #fff;
      }

      .forum-item {
        display: inline-block;
        width: 250px;
        height: 120px;
        border: 1px solid #eee;
        padding: 5px;
        overflow: hidden;
        box-sizing: border-box;
        margin: 10px;
        .user {
          width: 100%;
          height: 40px;
          display: flex;
          align-items: center;
          img {
            width: 40px;
            height: 40px;
            border-radius: 50%;
          }
          span {
            margin-left: 10px;
            color: #757575;
          }
        }
        .text {
          margin-top: 10px;
          padding: 0px 10px;
          white-space: pre-wrap;
          text-overflow: ellipsis;
          overflow: hidden;
          display: -webkit-box;
          -webkit-line-clamp: 3;
          -webkit-box-orient: vertical;
        }
      }
    }
  }
  .live-box {
    width: 100%;
    margin-top: 50px;
    margin-bottom: 50px;
    .title {
      font-size: 0.42667rem;
      margin: 0.23333rem 0.43333rem;
    }
    .live-list {
      width: 100%;
      height: auto;
      display: flex;
      flex-wrap: wrap;
      .live-item {
        flex-grow: 1;
        width: 120px;
        height: 190px;
        padding: 5px;
        margin-bottom: 10px;
        box-sizing: border-box;
        cursor: crosshair;
        &:nth-child(3n) {
          margin-right: 0px;
        }
        .auto-img {
          margin-top: 0px;
        }
        .content-text {
          .name {
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
            width: 100%;
            font-size: 13px;
          }
          .update {
            font-size: 12px;
            color: #999;
          }
        }
      }
    }
  }

  ::v-deep .van-nav-bar .van-icon {
    color: #999999;
    font-size: 18px;
  }
}
</style>